.main{
    width: 100%;
    height: 100%;
}

#home.home.section{
    padding: 6rem 0 12rem;
}

video{
    width: 100%;
    height: 100%;
    object-fit: cover;
    
}

.home__content{
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home__social-icon{
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover{
    color: var(--title-color-dark);

}

.home__title{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}

.home__hand{
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
}

.home__subtitle{
    position:relative;
    font-size: var(--h3-font-size);
    padding-left: 5.4rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
}

.home__subtitle::before {
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;

}

.home__description{
    max-width: 400px;
    margin-bottom: var(--mb-3-25);
}

.home__img{
    background: url(../../src/assets/dewa.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    animation: profile__animate 8s ease-in-out infinite 1s;
}

@keyframes profile__animate{
    0%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    
    50%{
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }

    
    100%{
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}

@media screen and (max-width: 992px) {
    .about__container{
        grid-template-columns: 1fr;
        row-gap: 2.5rem;
    }

    .about__image{
        width: 220px;
    }

    .about__data{
        text-align: center;
    }

    .about__info{
        justify-content: center;
    }

    .about__description{
        padding: 0 5rem;
        margin-bottom: 2rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 768px) {
    .home__content{
        grid-template-columns: 116px repeat(1, 1fr);
        align-items: center;
    }

    .home__img{
        background: url(../../src/assets/dewa.jpg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
        order: 1;
        justify-self: center;
        width: 240px;
        height: 240px;
        animation: profile__animate 8s ease-in-out infinite 1s;
    }

   .about__image{
    width: 300px;
}
  }
  
  @media screen and (max-width: 576px) {
  }
  
  /* For small devices */
  @media screen and (max-width: 350px) {
    .home__content{
        grid-template-columns: 116px repeat(3, 1fr);
        padding-top: 5.5rem;
        column-gap: 2rem;
        align-items: center;
    }

   .about__image{
    width: 150px;
}
  }
  